@media screen and (max-width: 600px) {
  .content {
    display: flex;
    flex: 1;
    width: 100%;
    overflow-y: auto;
    justify-content: normal;
    padding: 12px;
    background-color: #fff;
  }

  .list {
    width: 100%;
    flex: 1;
  }

  .input-container {
    display: flex;
    align-items: center;
    padding: 0px 12px 12px 12px;
    background: white;
    display: flex;
  }

  .input-content {
    display: flex;
    flex-flow: column;
    /* gap: 12px; */
    align-items: center;
    flex: 1;
    padding: 8px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px 2px;
    box-sizing: border-box;
  }
}

@media screen and (min-width: 600px) {
  .content {
    display: flex;
    flex: 1;
    /* flex-flow: column; */
    width: 100%;
    overflow-y: auto;
    justify-content: center;
    padding: 24px;
    background-color: #fff;
  }

  .list {
    width: 100%;
    flex: 0.7 1;
  }

  .input-container {
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 24px; */
    padding-bottom: 24px;
    /* border-top: 1px solid #f0f0f0; */
    background: white;
    display: flex;
  }

  .input-content {
    display: flex;
    flex-flow: column;
    /* gap: 12px; */
    align-items: center;
    flex: 0.7 1;
    padding: 15px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px 2px;
    box-sizing: border-box;
  }
}

body {
  background-image: url('../public/bg.png');
  background-size: cover;
  background-position: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('../public/bg.png');
  background-size: cover;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
body {
  animation: fadeIn 2s ease-in-out;
}

.layout-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #ffff;
}

.header {
  background: none;
  padding: 0 12px;
  box-shadow: 1px 0px 7px 0px rgba(0, 0, 0, 0.3);
  z-index: 10;
}

.form-content {
  display: flex;
  gap: 6px;
  justify-content: center;
  margin-bottom: 20px;
}

.think-content {
  border-left: 3px solid gray;
  padding-left: 10px;
  box-sizing: border-box;
}

.think-title {
  margin: 0;
  width: 100%;
  color: grey;
  text-align: left;
}

.think-text {
  margin: 0;
  font-size: 12px;
  width: 100%;
  /* max-height: 200px; */
  /* overflow: scroll; */
  color: grey;
  text-align: left;
}

.result-content {
  margin-top: 15px;
}

.message-item {
  display: flex !important;
  padding: 20px 0 !important;
}

.message-container {
  display: flex;
  width: 100%;
  /* max-width: 800px; */
  margin: 0 auto;
}

.message-content {
  width: 100%;
  /* margin-left: 16px; */
  flex: 1;
}

.user-content {
  text-align: right;
}

.markdown-content {
  padding: 12px 16px;
  border-radius: 8px;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

pre {
  width: 100%;
  overflow: auto;
}

.user .message-container {
  flex-direction: row-reverse;
}

.user .message-content {
  margin-left: 0;
}

.ant-collapse-header {
  padding-bottom: 0px;
}

.user .markdown-content {
  background: #e6f7ff;
}

.timestamp {
  color: #666;
  font-size: 12px;
  margin-top: 4px;
}

.bot-timestamp {
  text-align: left;
}

.user-timestamp {
  text-align: right;
}

.user-question {
  display: inline-block;
  max-width: 100%;
  min-width: 40px;
  color: #ffff;
  text-align: center;
  padding: 10px;
  border-radius: 15px;
  background-color: #36393d;
}

.input {
  padding-left: 0px;
  border: none;
  margin-bottom: 5px;
}

.input:focus {
  border: none;
  box-shadow: none;
}

.input:active {
  border: none;
  box-shadow: none;
}

.input-op {
  display: flex;
  width: 100%;
  flex: 1;
  justify-content: flex-end;
}

.send-button {
  /* align-self: flex-end; */
  /* margin-bottom: 4px; */
}

